body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100%;
  background: #f2f2f2;
}
div.ui.segment.header {
  background: #132747 !important;
  color: white !important;box-shadow: 5px 5px 10px #888888;
  border-radius: none !important;
}
div.ui.segment.footer {
  background: #1c2026 !important;
  color: white !important;
  border-radius: none !important;
  margin-top: 50px !important;
}
span {
  float: right;
}
a.link {
  text-decoration: none;
  color: white;
  margin-right: 10px;
  padding: 10px;
}
a.link:hover {
  border-bottom: 1px solid white;
}
.background {
    min-height: 100%;
    width:100%;
    left: 0;
    position: fixed;
    background: -webkit-linear-gradient(top, #132747 0%, #01173a 50%, #132747 100%);
    background: linear-gradient(top, #132747 0%, #01173a 50%, #132747 100%);
}
h4.ui.center.aligned.header {
    color: white !important;
    margin-top: 100px !important;
}
div.ui.fluid.left.icon.input input  {
    padding: 20px !important;
    color: white !important;
    background-color: transparent !important;
    outline: none;
    border:0 !important;
    border-bottom: 2px solid white !important;
}

div.field.input-custom input::-webkit-input-placeholder {
    color: white !important;
    opacity: .8;
    font-size: 16px;
}

div.field.input-custom input::-ms-input-placeholder {
    color: white !important;
    opacity: .8;
    font-size: 16px;
}

div.field.input-custom input::placeholder {
    color: white !important;
    opacity: .8;
    font-size: 16px;
}
div.field.input-custom {
    box-shadow: none !important;
    background: transparent !important;
}
div.ui.message {
    background-color: transparent !important;
    border: none !important;
    outline: none !important;
    box-shadow: none !important;
    padding: 0 !important;
}
div.ui.message h3 {
    width: auto;
    float: left;
    padding:5px;
    color: white;
    font-weight: normal;
}
div.ui.message a {
    color: white !important;
    float: right;
    border: 2px solid white;
    border-radius: 10px;
    padding: 5px;
    margin: 0;
}
div.ui.message a:hover {
    background-color: white;
    color: #0c9999 !important;
}
div.ui.segment.update {
    margin: 10px !important;
    overflow: hidden !important;
    font-size: 20px !important;
    vertical-align: baseline !important;
    width: 100% !important;
}
div.ui.segment.update:hover {
    background-color: #f2f4f7;
    cursor: pointer;
}
div.ui.segment.update > button.ui.positive.button {
    float: right !important;
    background-color: #1e8c7d !important;
}
div.ui.segment.update > button.ui.negative.button {
    float: right !important;
    background-color: #f41f1f !important;
}

div.ui.segment.update > button.ui.positive.button:hover, button.ui.negative.button:hover {
    box-shadow: 2px 2px 5px black !important;
}

button.ui.button.add {
    float: right;
    margin-bottom: 20px;
    background: #1c2026;
    color: white !important;
}

select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}
div.content {
    overflow: hidden !important;
}
div.options {
    font-family: Consolas;
    background: #1c2026;
    font-size: 16px;
    padding:8px;
    margin:5px;
    float: left;
    color: white;
    border-radius: 10px;
}

div.title {
    float: left;
    font-size: 16px;
    padding:8px;
    margin:5px;
}

button.ui.button.delete-button {
    background: none !important;
    padding: 1px !important;
    margin: 0 !important;
    color: white !important;
    margin-left: 10px !important;
    font-size: 16px !important;
    margin-right: 0 !important;
}

div.ui.segment.questions-block h3 {
    width: 30% !important;
    float: left;
    padding: 10px;
}
